<template>
  <div class="items" >
 <slot name="noSelected"></slot>
<slot name="Selected"></slot>
    <p v-text="text" :class="{activecolor:flag}"></p>
  </div>
</template>
<script>
  export default {
    props: ['text','flag'],
  }
</script>
<style scoped>
  .items {
    width: 25%;
    height: 2.7rem;
    float: left;
    text-align: center;
    padding: 0.1rem 0;
    box-sizing: border-box;
  }

  .items img {
    width: 2rem;
    height: 1.2rem;
  }

  .items p {
    font-size: 0.8rem;
  }

  .items::after {
    content: '';
    display: inline-block;
    width: 0;
    height: 0;
    clear: both;
  }
  .activecolor{
    color: blue;
  }
</style>



