<template>
<div>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="item in arrData">
        <img :src="item.srcs" alt="">
      </div>
    </div>
    <!--  -->
    <div class="swiper-pagination"></div>
  </div>
</div>
</template>
<style scoped>
  @import "../assets/Swiper-3.4.2/dist/css/swiper.min.css";
  .swiper-slide > img {
    width: 100%;
    height: 12rem;
  }
</style>
<script>
  import '../assets/Swiper-3.4.2/dist/js/swiper.min'
  export default {
    data:function () {
      return{
        arrData:[
          {
            srcs:require("../assets/images/img/banner1.jpg")
          },
          {
            srcs:require("../assets/images/img/banner2.jpg")
          },
          {
            srcs:require("../assets/images/img/banner3.jpg")
          }
        ]
      }
    },
    mounted: function () {
       new Swiper('.swiper-container', {
        loop: true,
        autoplay:2000,
        // 
        pagination: '.swiper-pagination',
      })
    }
  }
</script>



