            </router-link>
          </li>
          <li class="pic2">
            <router-link to="/destination/guonei">
              <span></span>
              <p :class="{activecolors:true}"></p>
            </router-link>
          </li>
          <li class="pic3">
            <router-link to="/destination/guowai">
              <span></span>
              <p :class="{activecolors:true}"></p>
            </router-link>
          </li>
        </ul>
      </div>
      <div class="destination-pics">
        <router-view>
        </router-view>
      </div>
