    width: 3rem;
    height: 3rem;
    background: url("../../assets/images/icon/sprites.png") -453px -170px no-repeat;
    /*background: url("../../assets/images/icon/sprites.png") -453px -100px no-repeat;*/
    margin: 0 auto;
  }

  .pic2 span {
    display: block;
    width: 3rem;
    height: 3rem;
    margin: 0 auto;
    background: url("../../assets/images/icon/sprites.png") -538px -170px no-repeat;
  }

  .pic3 span {
    display: block;
    width: 3rem;
    height: 3rem;
    margin: 0 auto;
    background: url("../../assets/images/icon/sprites.png") -623px -170px no-repeat;
  }
  /**/
  /*.activecolors{*/
    /*color: blue;*/
  /*}*/
  .router-link-active{
    color: red;
  }
</style>



