import Vue from 'vue'
import Router from 'vue-router'
// import ToolBar from '../components/ToolBar.vue'
//
import Home from '../components/pages/home.vue'
import Destination from '../components/pages/destination.vue'
import Viewhistory from '../components/pages/viewhistory.vue'
import Mine from '../components/pages/mine.vue'
//
import Guonei from '../components/page/guonei.vue'
import Hot from '../components/page/hot.vue'
import Guowai from '../components/page/guowai.vue'
// import
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name:'Home',
      component:Home
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path:'/destination',
      name:'Destination',
      component:Destination,
      children:[
        {
          path:'/',
          name:'Hot',
          component:Hot
        },
        {
          path:'Guonei',
          name:'Guonei',
          component:Guonei
        },{
          path:'Guowai',
          name:'Guowai',
          component:Guowai
        },{
          path:'Hot',
          name:'Hot',
          component:Hot
        }
      ]
    },
    {
      path: '/viewhistory',
      name: 'Viewhistory',
      component: Viewhistory,
    },{
    path:'/mine',
      name:'Mine',
      component:Mine
    }
  ]
})



