<template>
  <div class="hotbox">
    <div class="hotbox-left">
      <img :src="src1" alt="">
      <p v-text="title1"></p>
      <div class="xiwei-box">
        <span v-text="title2"></span>
        <div class="price-text">
          <span v-text="title3"></span><span v-text="title4"></span><span v-text="title5"></span>
        </div>
      </div>
    </div>
    <div class="hotbox-right">
      <img :src="src2" alt="">
      <p v-text="title1"></p>
      <div class="xiwei-box">
        <span v-text="title2"></span>
        <div class="price-text">
          <span v-text="title3"></span><span v-text="title4"></span><span v-text="title5"></span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props:['src1','title1','title2','title3','title4','title5','src2']
  }
</script>
<style>
  .hotbox{
    width: 100%;
    display: flex;
    flex-direction:row;
    justify-content:space-around;
  }
  .hotbox-left,.hotbox-right{
    width: 47%;
    border:1px solid #cccccc;
    border-top: none;
    margin-top: 0.5rem;
  }
  .hotbox-left>img,.hotbox-right>img{
    width: 100%;
  }
  .hotbox-left>p,.hotbox-right>p{
    font-size: 0.7rem;
  }
  .xiwei-box{
    font-size: 0.6rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0.2rem 0;
  }
  .xiwei-box>span{
    color: #ccc;
  }
  .price-text{
    padding:0 0.2rem;
  }
  .price-text>span:first-child{
    color: orange;
    font-size: 0.1rem;
  }
  .price-text>span:nth-of-type(2){
    color: orange;
    font-size: 0.7rem;
  }
  .price-text>span:last-child{
    color: #cccccc;
    font-size: 0.5rem;
  }
</style>



