    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .box-list>.box-list-left>img{
   transform: scale(0.6);
  }
  .box-list-right{
    margin-right: 1rem;
  }
  .box-list-right>span{
    font-size: 2rem;
    color: #e8e8e8;
  }

</style>



