<template>
  <div class="toolbar">
    <ul>
      <li v-for="(item,i) in arrData" @click="fk(item.path)">
        <router-link :to="item.path">
          <ToolBarItems :text="item.text" :index="item.path" :flag="paths==item.path">
            <img :src="item.src1" alt="" slot="noSelected" v-show="paths!=item.path">
            <img :src="item.src2" alt="" slot="Selected" v-show="paths==item.path">
          </ToolBarItems>
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
  import ToolBarItems from './ToolBarItems.vue';
  export default {
    components: {
      ToolBarItems
    },
    data: function () {
      return {
        arrData: [
          {
            text: '',
            path: '/home',
            src1: require("../assets/images/icon/footer_0322_05.png"),
            src2: require('../assets/images/icon/footer_0322_01.png')
          },
          {
            text: '',
            path: '/destination',
            src1: require("../assets/images/icon/footer_0322_06.png"),
            src2: require('../assets/images/icon/footer_0322_02.png')
          },
          {
            text: '',
            path: '/viewhistory',
            src1: require("../assets/images/icon/footer_0322_07.png"),
            src2: require('../assets/images/icon/footer_0322_03.png')
          },
          {
            text: '',
            path: '/mine',
            src1: require("../assets/images/icon/footer_0322_08.png"),
            src2: require('../assets/images/icon/footer_0322_04.png')
          }
        ],
        paths: ''
      }
    },
    methods: {
      fk: function (val) {
        this.paths = val;
      }
    },
    created: function () {
      if (window.location.hash.substr(1) === '/') {
        this.paths = '/home';
      }
      else {
        this.paths = window.location.hash.substr(1)
      }
    }
  }
</script>
<style scoped>
  .toolbar {
    width: 100%;
    height: 2.7rem;
    background: #cccccc;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 99;
  }
</style>



